import React from "react";
import styled from "@emotion/styled/macro";

const Style = styled.div`
  input {
    appearance: none;
    padding: 0;
    display: inline-block;
    vertical-align: middle;
    user-select: none;
    flex-shrink: 0;
    height: ${({ size }) => (size ? size : "1.25rem")};
    width: ${({ size }) => (size ? size : "1.25rem")};
    border-width: 1px;
    border-color: #374151;
    outline: none;

    &:checked {
      background-color: #ca7c02ff;
      background-size: 100% 100%;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
  input[type="radio"]:checked + label {
    color: #ffff;
  }
`;
const Radio = React.forwardRef(({ size, text, id, type, ...props }, ref) => (
  <Style
    className="flex relative items-center justify-center z-50"
    style={{ width: size }}
    size={size}
  >
    <input {...props} type={type} ref={ref} id={id} />
    {(text || text === 0) && (
      <label htmlFor={id} className="absolute text-gray-800 ">
        {text}
      </label>
    )}
  </Style>
));

export default Radio;
