import React, { useState } from "react";
import Header from "components/Header";
import Radio from "components/Radio";
import { useForm } from "react-hook-form";
import { questionRadios } from "static/payloads/questions";
import Dots from "components/Dots";

const Questions = () => {
  const { register, handleSubmit } = useForm();
  const [page, setPage] = useState(0);

  const onSubmit = (responses) => {

    if ((page + 1) * 8 >= questionRadios.length) {
      console.log("responses: ", responses);
    } else {
			window.scrollTo(0, 100)
      setPage(page + 1);
    }
  };

  const Radios = (id) => {
    const radios = [];

    for (let r = 0; r < 5; r++) {
      radios.push(
        <Radio
          type="radio"
          {...register(`${id}`)}
          size="2rem"
          text={r}
          value={r}
          id={`${id}-${r}`}
          key={`${id}-${r}`}
        />
      );
    }
    return radios;
  };

  const form = () => {
    const completeForm = [];
    for (let q = page * 8; q < page * 8 + 8; q++) {
      if (questionRadios[q]) {
        completeForm.push(
          <div className="my-8" key={q}>
            <div className="flex items-center">
              <hr className="border-t border-tertiary w-36 mr-5" />
              <span className="text-gray-800 w-3/5">{`${q + 1}. ${
                questionRadios[q]?.text
              }`}</span>
            </div>

            <div className="flex justify-between max-w-13-rem mt-4 ml-41">
              {Radios(q)}
            </div>
          </div>
        );
      }
    }
    return completeForm;
  };

  return (
    <>
      <Dots />
      <div className="flex flex-col items-center min-h-screen bg-tertiary">
        <Header />
        <div className="flex sticky top-0 z-30 items-center flex-col container max-w-6xl 2xl:max-w-screen-xl bg-secondary py-7 z-40">
          <p className="text-white text-lg mb-6">
            Para cada pergunta você deverá escolher uma resposta única, de
            acordo com as opções abaixo:
          </p>
          <div className="flex text-white justify-between">
            <div className="flex items-center max-w-7-rem mr-5">
              <div className="flex items-center justify-center min-w-2-rem h-8 border mr-2">
                <span>0</span>
              </div>
              <span className="text-sm">Discordo totalmente</span>
            </div>
            <div className="flex items-center max-w-8-rem mr-5">
              <div className="flex items-center justify-center min-w-2-rem h-8 border mr-2">
                <span>1</span>
              </div>
              <span className="text-sm">Discordo parcialmente</span>
            </div>
            <div className="flex items-center max-w-9-rem mr-5">
              <div className="flex items-center justify-center min-w-2-rem h-8 border mr-2">
                <span>2</span>
              </div>
              <span className="text-sm">Não concordo e nem discordo</span>
            </div>
            <div className="flex items-center max-w-8-rem mr-5">
              <div className="flex items-center justify-center min-w-2-rem h-8 border mr-2">
                <span>3</span>
              </div>
              <span className="text-sm">Concordo parcialmente</span>
            </div>
            <div className="flex items-center max-w-8-rem">
              <div className="flex items-center justify-center min-w-2-rem h-8 border mr-2">
                <span>4</span>
              </div>
              <span className="text-sm">Concordo totalmente</span>
            </div>
          </div>
        </div>
        <div className="flex flex-grow container max-w-6xl 2xl:max-w-screen-xl bg-white py-14">
          <form
            className="flex flex-col flex-grow justify-between items-center z-30"
            onSubmit={handleSubmit(onSubmit)}
          >
            <div className="flex flex-col w-full">
              <div className="flex flex-col">{form()}</div>
              <div className="flex items-center justify-center">
                <button
                  className="bg-primary text-white text-lg px-14 py-3 border-secondary-dark border mt-10 transform active:scale-98 z-50"
                  type="submit"
                >
                  AVANÇAR
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </>
  );
};

export default Questions;
